<script>
export default {
  name: "Configurator",

  computed: {
    sumPrice: function () {
      // `this` points to the vm instance
      return this.stepPrice1 + this.stepPrice2 + this.stepPrice3;
    },
    currentPrice: function () {
      // `this` points to the vm instance
      if (this.isModern) return this.modernPrice;
      if (!this.isModern) return this.orientalPrice;

      return 0;
    },
  },
  data() {
    return {
      stepPrice1: 0,
      stepPrice2: 0,
      stepPrice3: 0,
      stepPriceSum: 0,
      isModern: false,
      open: true,
      e6: 1,
      images: {
        orientalisch: require("../assets/oriental.jpg"),
        modern: require("../assets/modern.jpg"),

        ice: require("../assets/ice.jpg"),
      },
      orientalPrice: [
        { name: "Al Waha", price: 2.6 },
        { name: "Chaos", price: 2.9 },
        { name: "Al Fakher", price: 2.5 },
        { name: "Nameless", price: 2.5 },
        { name: "Chillma", price: 2.6 },
      ],
      modernPrice: [
        { name: "187 ", price: 3.0 },
        { name: "Babos", price: 3.0 },
        { name: "Adalya", price: 2.6 },
        { name: "7Days", price: 2.0 },
        { name: "Aqua Menta", price: 2.9 },
        { name: "True Passion", price: 2.9 },
        { name: "Zomo", price: 1.9 },
      ],
      selectedPrice: [],
    };
  },
};
</script>